@import '../../base/base';

.clients {
  margin-bottom: 60px;
  &__title {
    @extend %section-title;
    margin-bottom: 4px;
  }

  &__list {
    list-style-type: none;
    padding: 40px 12px;
    margin: 0;

    display: flex;
    gap: 50px;
    align-items: center;

    overflow-x: auto;

    scroll-snap-type: x mandatory;
    -webkit-overflow-scrolling: touch;
    // скрываем скролл-бар
    -ms-overflow-style: none;
    scrollbar-width: none;
    &::-webkit-scrollbar {
      width: 0;
      height: 0;
    }

    &-item {
      width: 125px;
      height: 83px;

      scroll-snap-align: center;
      scroll-snap-stop: always;
      object-fit: contain;
      object-position: center center;
    }
  }
}
