$primary-color: #4a4949;
$secondary-color: #c52411;
$alternate-color: #383c41;

%section-title {
  color: $primary-color;
  text-align: center;
  font-size: 22px;
  font-weight: 700;
  line-height: normal;
  margin: 0;
  text-align: start;
}

%list-item {
  color: $primary-color;
  font-size: 16px;
  font-weight: 600;
  line-height: normal;
}

@media screen and (max-width: 425px) {
  %section-title {
    font-size: 20px;
  }
}
