@keyframes border-animation{100%{transform:rotate(1turn)}}.deeplink{text-decoration:none}.deeplink:has(.deeplink__buy-button){margin:0 0 0 13%;width:100%}.deeplink__animated-button{position:relative;z-index:0;overflow:hidden;display:flex;align-items:center;justify-content:center;margin-top:4px;width:max-content;color:#c52411;text-align:center;font-size:14px;font-weight:600;line-height:normal;letter-spacing:1.96px;padding:6px 12px;box-sizing:border-box;border-radius:12px}.deeplink__animated-button::before{content:"";position:absolute;z-index:-2;left:-50%;top:-350%;width:200%;height:800%;background-color:#fff;background-repeat:no-repeat;background-size:50% 50%,50% 50%;background-position:0 0,100% 0,100% 100%,0 100%;background-image:linear-gradient(90deg, #c52411 65%, #fff),linear-gradient(90deg, #fff, #c52411 35%),linear-gradient(270deg, #c52411 65%, #fff),linear-gradient(270deg, #fff, #c52411 35%);animation:border-animation 4s ease-in-out infinite}.deeplink__animated-button::after{content:"";position:absolute;z-index:-1;left:2.25px;top:2px;width:calc(100% - 4px);height:calc(100% - 4px);background:#fff;border-radius:10px}.deeplink__buy-button{display:flex;flex-direction:column;justify-content:center;align-items:center;width:100%;height:42px;border-radius:16px;background-color:#c52411;transition:transform .2s}.deeplink__buy-button:active{transform:scale(0.95)}.deeplink__buy-button::after{content:"Купить";color:#fff;text-align:center;font-size:16px;font-weight:700;line-height:normal}.deeplink__icon{width:44px;height:44px;background-position:center;background-repeat:no-repeat}.deeplink__icon_type_whatsapp{background-image:url("../../images/icons/whatsapp_icon.svg")}.deeplink__icon_type_telegram{background-image:url("../../images/icons/telegram_icon.svg")}.deeplink__icon_type_facebook{background-image:url("../../images/icons/facebook_icon.svg")}.deeplink__icon_type_instagram{background-image:url("../../images/icons/instagram_icon.svg")}.deeplink__app-frame{display:none}